.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-top: 50px;
}

.App-header h1 {
  border-bottom: 4px solid aqua;
}

.App-link {
  color: #61dafb;
}

.Navbar {
  display: flex;
  justify-content: space-around;
  background-color: #282c34;
  border-bottom: #61dafb 2px solid;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}

.Navbar-title {
  color: White;
}

.Navbar-link {
  margin-top: 20px;
}

.Navbar-link a {
  color: white;
  font-size: 30px;
  margin: 10px;
  text-decoration: none;

}

.Navbar-link a:hover {
  color: aqua;
}

.Movie-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.Movie-wrapper {
  width: 500px;
  background-color: rgb(222, 222, 251);
  color: black;

}

.Movie-search {
  height: 60px;
  font-size: 2rem;
  margin-bottom: 2rem;
  padding: 0.3rem;
  width: 500px;
}

.Movie-title {
  display: inline-block;
  font-size: 25px;
  margin: 20px;
  font-weight: bold;
}

.Movie-date {
  font-style: italic;
  font-size: 25px;
}

.Movie-rate {
  color: red;
  margin: 10px;
}
